<template>
  <div class="home-banner">
    <XtxCarousel :sliders="sliders" autoPlay></XtxCarousel>
  </div>
</template>

<script>
import { ref } from 'vue'
import { findBanner } from '@/api/home'
export default {
  name: 'home-banner',
  setup (props) {
    const sliders = ref([])
    findBanner()
      .then(res => {
        sliders.value = res.result
      })
      .catch(err => {
        console.log(err)
      })
    return { sliders }
  }
}
</script>

<style lang="less" scoped>
.home-banner {
  width: 1240px;
  height: 500px;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 98;
}
.xtx-carousel {
  ::v-deep .carousel-btn.prev {
    left: 270px;
  }
  ::v-deep .carousel-indicator {
    padding-left: 250px;
  }
}
</style>
